  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

.loader {
  position: fixed;
  top: 0;
  bottom: 0;
  display: flex;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #ffffff;
  transition: all 1.5s;
  z-index: 3; }
  .loader__logo {
    display: block;
    width: 70%; }
  .loader__enter {
    position: absolute;
    bottom: 100px;
    font-family: Neue Machina;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 22px;
    color: #000000; }
    .loader__enter--web {
      display: block; }
    .loader__enter--wap {
      display: none; }
  .loader__logo, .loader__enter {
    animation: 4s showItems; }

@keyframes showItems {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@media screen and (max-width: 768px) {
  .loader__enter--web {
    display: none; }
  .loader__enter--wap {
    display: block; } }

.header {
  position: fixed;
  display: block;
  width: 100%;
  background-color: #ffffff;
  z-index: 2; }
  .header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    align-content: space-between;
    padding: 25px 46px 25px 35px;
    width: 1024px;
    max-width: 100%;
    margin: 0 auto; }
  .header__logo {
    display: inline-block;
    height: 23px;
    width: 17px; }

.nav-menu {
  display: flex;
  gap: 24px;
  background-color: #ffffff; }
  .nav-menu__item {
    position: relative;
    font-family: "Neue Machina", "Arial", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 14px;
    color: #000000; }
    .nav-menu__item--hyper:after {
      position: absolute;
      top: -2px;
      right: -10px;
      content: url("../img/svg/hyper.svg"); }
    .nav-menu__item:hover {
      text-decoration: underline; }

.burger-menu {
  position: relative;
  display: none;
  width: 30px;
  height: 2px;
  background-color: #2c39f2;
  transition: all .7s; }
  .burger-menu::before {
    position: absolute;
    top: -8px;
    left: 0;
    display: block;
    width: 100%;
    height: 2px;
    content: "";
    background-color: #2c39f2;
    transition: all .7s; }
  .burger-menu::after {
    position: absolute;
    top: 8px;
    left: 0;
    display: block;
    width: 100%;
    height: 2px;
    content: "";
    background-color: #2c39f2;
    transition: all .7s; }
  .burger-menu--close {
    background-color: #ffffff; }
    .burger-menu--close::before {
      top: 1px;
      transform: rotate(-45deg); }
    .burger-menu--close::after {
      top: 1px;
      transform: rotate(45deg); }

@media screen and (max-width: 768px) {
  .header__wrapper {
    padding: 25px 35px; }
  .nav-menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    gap: 30px;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transform: translateY(-100%);
    transition: all .8s;
    z-index: -1; }
    .nav-menu__item {
      font-size: 28px;
      line-height: 28px; }
  .burger-menu {
    display: block; } }

.main {
  display: block;
  background-color: #ffffff; }
  .main__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    align-content: space-between;
    padding: 120px 35px;
    width: 1024px;
    max-width: 100%;
    height: 100vh;
    margin: 0 auto;
    gap: 40px; }
    .main__wrapper--blue {
      position: absolute;
      left: 0;
      top: 0;
      display: none;
      padding-top: 25%;
      background-color: #2c39f2;
      transform: translateX(-100%);
      transition: all .8s;
      z-index: 1; }
  .main__heading {
    width: 562px;
    max-width: 100%; }
    .main__heading h1 {
      margin: 0 auto;
      font-family: "Neue Machina", "Arial", sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 96px;
      line-height: 96px;
      color: #2c39f2; }
  .main__info {
    width: 352px;
    margin: 0 auto; }
    .main__info h2 {
      font-family: "Neue Machina", "Arial", sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 30px;
      line-height: 40px;
      color: #000000; }
    .main__info--blue h2 {
      color: #ffffff; }

@media screen and (max-width: 768px) {
  .main__wrapper {
    padding-top: 30%; }
    .main__wrapper--blue {
      display: block; }
  .main__info {
    display: none; }
    .main__info--blue {
      display: block; } }

